html{
  scroll-snap-type: y mandatory;
}

.App{
  background-color: black;
  height: 100vh;
  display: grid;
  place-items: center;
}

.app__videos{
  height: 98vh;
  max-height: 700px;
  width: 70%;
  max-width: 500px;
  border: 1px solid black;
  border-radius: 20px;
  overflow: scroll;
  scrollbar-width: none;
  scroll-snap-type: y mandatory;
  position: relative;
}

 /*Esconde a scrollbar no chrome, safari e opera*/

 .app__videos::-webkit-scrollbar{
  display: none;}
    
  /*Esconde a scrollbar no edge e firefox*/

  .app__videos{
  -ms-overflow-style: none;
  scrollbar-width: none;}

@media only screen and (max-width:440px) {
  .app__videos{
    width: 99%;
    height: 99vh;
  }
}